.header__logoContainer i {
  display: block;
  font-size: 3.9rem;
  margin-right: 1.4rem;
  background: linear-gradient(to left, #08ec1c, #ffca33, #2f99ff, #ff4242);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  -webkit-animation-name: logoAnimationRotate;
          animation-name: logoAnimationRotate;
  -webkit-animation-duration: 20s;
          animation-duration: 20s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

@-webkit-keyframes logoAnimationRotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes logoAnimationRotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes logoAnimationColors {
  0% {
    color: #08ec1c;
  }

  25% {
    color: #2f99ff;
  }

  50% {
    color: #ff4242;
  }

  75% {
    color: #ffca33;
  }

  100% {
    color: #08ec1c;
  }
}

@keyframes logoAnimationColors {
  0% {
    color: #08ec1c;
  }

  25% {
    color: #2f99ff;
  }

  50% {
    color: #ff4242;
  }

  75% {
    color: #ffca33;
  }

  100% {
    color: #08ec1c;
  }
}

