.navside__menu a {
  font-size: 1.5rem;
  padding-right: 4.8rem;
  font-weight: 400;
  display: block;
  color: #6a6a6a;
  transform: rotate(-90deg);
}

.navside__menu a:hover {
  color: var(--textColor);
}

.navside__menu a:not(:first-child) {
  margin-top: 10rem;
}

.navside__menu a::after {
  content: "";
  width: 0px;
  height: 0.1rem;
  display: block;
  background: transparent;
  transition: 500ms;
}

.navside__menu a:hover::after {
  width: 100%;
  background: var(--textColor);
}

@media (min-width: 1200px) {
  .navside {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    position: fixed;
    left: -3rem;
    top: 2rem;
    opacity: 0;
    visibility: hidden;
    transition: all 1500ms ease-out;
  }
}

.footer {
  margin: 12rem auto 0;
  border-top: 0.4rem solid;
}

.footer__icons {
  margin-top: 3.2rem;
  display: flex;
  justify-content: center;
}
.footer__icons i {
  font-size: 3rem;
  padding: 1.6rem;
}

.footer-credit {
  margin-top: 1rem;
  text-align: center;
  font-size: 1.8rem;
}

@media (min-width: 760px) {
  .footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .footer__icons {
    margin-top: 1rem;
  }

  .footer__icons i {
    font-size: 3.5rem;
    padding: 0 2rem 0 0;
  }
}

@media (min-width: 1200px) {
  .footer {
    margin-bottom: 3rem;
    padding: 1.6rem 1rem;
  }
}

